import { reactive } from 'vue';

// Cration d'un systme d'vnements simple pour Vue 3
export const eventBus = reactive({
  // Stockage des couteurs d'vnements
  listeners: {},

  // Mthode pour mettre un vnement
  emit(event, ...args) {
    if (this.listeners[event]) {
      this.listeners[event].forEach(callback => {
        callback(...args);
      });
    }
  },

  // Mthode pour s'abonner  un vnement
  on(event, callback) {
    if (!this.listeners[event]) {
      this.listeners[event] = [];
    }
    this.listeners[event].push(callback);
    
    // Retourne une fonction pour se dsabonner
    return () => {
      this.listeners[event] = this.listeners[event].filter(
        listener => listener !== callback
      );
    };
  },

  // Mthode pour se dsabonner de tous les vnements d'un certain type
  off(event) {
    if (this.listeners[event]) {
      delete this.listeners[event];
    }
  }
});

// Constantes pour les noms d'vnements
export const EVENTS = {
  DOCUMENT_DELETED: 'document:deleted',
  DOCUMENT_CREATED: 'document:created',
  DOCUMENT_UPDATED: 'document:updated',
  DOCUMENT_RESTORED: 'document:restored',
  DOCUMENT_FORCE_DELETED: 'document:force_deleted'
};
