<template>
    <div class="share-document-container" v-bind:class="{ 'visible': isVisible }">
        <div class="p-tabview">
            <TabView>
                <TabPanel header="Partager par email">
                    <div class="p-fluid share-content">
                        <div class="field">
                            <label for="email">Adresse e-mail du destinataire</label>
                            <InputText id="email" v-model="email" placeholder="Entrez l'adresse e-mail" />
                            <small v-if="emailError" class="p-error">{{ emailError }}</small>
                        </div>
                        
                        <div class="field">
                            <label for="expiration">Date d'expiration (optionnel)</label>
                            <Calendar id="expiration" v-model="expiration" dateFormat="dd/mm/yy" placeholder="Slectionnez une date d'expiration" :minDate="minDate" :showIcon="true" />
                        </div>
                        
                        <div class="field">
                            <label for="message">Message (optionnel)</label>
                            <Textarea id="message" v-model="message" rows="3" placeholder="Ajouter un message personnalis" />
                        </div>
                        
                        <div class="field-checkbox">
                            <Checkbox id="notification" v-model="notification" :binary="true" />
                            <label for="notification">M'envoyer une notification quand le document est consult</label>
                        </div>
                        
                        <div class="document-info" v-if="documentData">
                            <h5>Document  partager</h5>
                            <div class="document-details">
                                <div class="document-icon">
                                    <i :class="getIconForDocument(documentData)" style="font-size: 2rem"></i>
                                </div>
                                <div class="document-meta">
                                    <div class="document-name">{{ documentData.nom }}</div>
                                    <div class="document-type">{{ documentData.type_lisible }}</div>
                                    <div class="document-size">{{ formatSize(documentData.taille) }}</div>
                                </div>
                            </div>
                        </div>
                        
                        <div class="p-d-flex p-jc-end p-mt-3">
                            <Button label="Partager" icon="pi pi-share-alt" class="p-button-primary" @click="shareDocument" :loading="loading" />
                        </div>
                    </div>
                </TabPanel>

                <TabPanel header="Partager par service">
                    <div class="p-fluid share-content">
                        <div class="field">
                            <label for="service">Service destinataire</label>
                            <Dropdown id="service" v-model="selectedService" :options="services" optionLabel="nom" optionValue="id" placeholder="Slectionnez un service" />
                            <small v-if="serviceError" class="p-error">{{ serviceError }}</small>
                        </div>
                        
                        <div class="field">
                            <label for="serviceExpiration">Date d'expiration (optionnel)</label>
                            <Calendar id="serviceExpiration" v-model="serviceExpiration" dateFormat="dd/mm/yy" placeholder="Slectionnez une date d'expiration" :minDate="minDate" :showIcon="true" />
                        </div>
                        
                        <div class="field">
                            <label for="serviceMessage">Message (optionnel)</label>
                            <Textarea id="serviceMessage" v-model="serviceMessage" rows="3" placeholder="Ajouter un message personnalis" />
                        </div>
                        
                        <div class="field-checkbox">
                            <Checkbox id="serviceNotification" v-model="serviceNotification" :binary="true" />
                            <label for="serviceNotification">M'envoyer une notification quand le document est consult</label>
                        </div>
                        
                        <div class="document-info" v-if="documentData">
                            <h5>Document  partager</h5>
                            <div class="document-details">
                                <div class="document-icon">
                                    <i :class="getIconForDocument(documentData)" style="font-size: 2rem"></i>
                                </div>
                                <div class="document-meta">
                                    <div class="document-name">{{ documentData.nom }}</div>
                                    <div class="document-type">{{ documentData.type_lisible }}</div>
                                    <div class="document-size">{{ formatSize(documentData.taille) }}</div>
                                </div>
                            </div>
                        </div>
                        
                        <div class="p-d-flex p-jc-end p-mt-3">
                            <Button label="Partager avec le service" icon="pi pi-users" class="p-button-primary" @click="shareWithService" :loading="serviceLoading" />
                        </div>
                    </div>
                </TabPanel>

                <TabPanel header="Gnrer un lien">
                    <div class="p-fluid share-content">
                        <div class="field">
                            <label for="linkPermission">Niveau de permission</label>
                            <Dropdown id="linkPermission" v-model="linkPermission" :options="permissionOptions" optionLabel="label" optionValue="value" placeholder="Slectionnez le niveau d'accs" />
                        </div>
                        
                        <div class="field">
                            <label for="linkExpiration">Date d'expiration (optionnel)</label>
                            <Calendar id="linkExpiration" v-model="linkExpiration" dateFormat="dd/mm/yy" placeholder="Slectionnez une date d'expiration" :minDate="minDate" :showIcon="true" />
                        </div>
                        
                        <!-- Protection par connexion -->
                        <div class="field-checkbox">
                            <Checkbox id="requireLogin" v-model="requireLogin" :binary="true" />
                            <label for="requireLogin">Obliger la connexion pour accder au document</label>
                        </div>
                        
                        <!-- Protection par code -->
                        <div class="field-checkbox">
                            <Checkbox id="requireCode" v-model="requireCode" :binary="true" />
                            <label for="requireCode">Protger par code (6 chiffres)</label>
                        </div>
                        
                        <!-- Gnration de QR Code -->
                        <div class="field-checkbox">
                            <Checkbox id="generateQr" v-model="generateQr" :binary="true" />
                            <label for="generateQr">Gnrer un QR Code pour le partage</label>
                        </div>
                        
                        <!-- Champ code (visible seulement si requireCode est coch) -->
                        <div class="field" v-if="requireCode">
                            <label for="accessCode">Code d'accs (6 chiffres)</label>
                            <InputText 
                                id="accessCode" 
                                v-model="accessCode" 
                                placeholder="Entrez un code  6 chiffres (optionnel)" 
                                maxlength="6"
                                @input="validateAccessCode"
                            />
                            <small class="p-text-secondary">
                                Si vous ne renseignez pas de code, un code sera gnr automatiquement.
                            </small>
                            <small v-if="accessCodeError" class="p-error">{{ accessCodeError }}</small>
                        </div>
                        
                        <div class="field" v-if="shareLink">
                            <label for="generatedLink">Lien gnr</label>
                            <div class="link-container">
                                <InputText id="generatedLink" v-model="shareLink" readonly class="generated-link" />
                                <Button icon="pi pi-external-link" class="p-button-outlined open-btn" @click="openLink" title="Ouvrir le lien" />
                                <Button icon="pi pi-copy" class="p-button-outlined copy-btn" @click="copyToClipboard('link')" title="Copier le lien" />
                            </div>
                            <small class="p-text-secondary">
                                Ce lien permet d'accder au document selon les permissions dfinies. 
                                <a :href="shareLink" target="_blank" class="link-clickable">Cliquez ici pour ouvrir</a>
                            </small>
                        </div>
                        
                        <!-- Afficher le code d'accs si protection par code active -->
                        <div class="field" v-if="shareLink && generatedAccessCode">
                            <label for="generatedCode">Code d'accs gnr</label>
                            <div class="link-container">
                                <InputText id="generatedCode" v-model="generatedAccessCode" readonly class="generated-link" />
                                <Button icon="pi pi-copy" class="p-button-outlined copy-btn" @click="copyToClipboard('code')" title="Copier le code" />
                            </div>
                            <small class="p-text-secondary">
                                Partagez ce code avec les personnes autorises  accder au document.
                            </small>
                        </div>
                        
                        <!-- Afficher le QR Code si gnr -->
                        <div class="field qr-code-container" v-if="shareLink && qrCodeUrl">
                            <label>QR Code</label>
                            <div class="qr-code-wrapper">
                                <img :src="qrCodeUrl" alt="QR Code" class="qr-code-image" />
                                <p class="qr-code-instructions">
                                    Scannez ce QR Code pour accder directement au document.
                                </p>
                                <Button label="Tlcharger le QR Code" icon="pi pi-download" class="p-button-outlined" @click="downloadQrCode" />
                            </div>
                        </div>
                        
                        <div class="document-info" v-if="documentData">
                            <h5>Document  partager</h5>
                            <div class="document-details">
                                <div class="document-icon">
                                    <i :class="getIconForDocument(documentData)" style="font-size: 2rem"></i>
                                </div>
                                <div class="document-meta">
                                    <div class="document-name">{{ documentData.nom }}</div>
                                    <div class="document-type">{{ documentData.type_lisible }}</div>
                                    <div class="document-size">{{ formatSize(documentData.taille) }}</div>
                                </div>
                            </div>
                        </div>
                        
                        <div class="p-d-flex p-jc-end p-mt-3">
                            <Button label="Gnrer le lien" icon="pi pi-link" class="p-button-primary" @click="generateShareLink" :loading="linkLoading" />
                        </div>
                    </div>
                </TabPanel>

            </TabView>
        </div>
    </div>
</template>

<script lang="ts">
import { defineComponent, inject } from 'vue';
import InputText from 'primevue/inputtext';
import Textarea from 'primevue/textarea';
import Checkbox from 'primevue/checkbox';
import Calendar from 'primevue/calendar';
import Button from 'primevue/button';
import Dropdown from 'primevue/dropdown';

import TabView from 'primevue/tabview';
import TabPanel from 'primevue/tabpanel';
import { useToast } from 'primevue/usetoast';
type ToastService = ReturnType<typeof useToast>;

import apiClient from '@/services/api';

interface Document {
    uuid: string;
    id?: string;  // Ajout de l'ID optionnel pour les documents provenant d'autres vues
    nom: string;
    type_lisible: string;
    taille: number;
    proprietaire: {
        nom: string;
    };
    created_at: string;
    chemin: string;
}

interface DialogRef {
    data: {
        document: Document;
    };
    close: (params: { refresh?: boolean }) => void;
}

interface Share {
    id: number;
    permission: 'read' | 'write';
    user?: { name: string; email: string };
    email?: string; // Can be present for email shares
    expiration_date: string | null;
    permission_label?: string;
}

export default defineComponent({

    name: 'ShareDocument',
    components: {
        InputText,
        Textarea,
        Checkbox,
        Calendar,
        Button,
        Dropdown,

        TabView,
        TabPanel,
    },
    data() {
        return {
            dialogRef: null as DialogRef | null,
            toast: null as ToastService | null,
            // Partage par email
            email: '',
            message: '',
            notification: false,
            loading: false,
            emailError: '',
            isVisible: false,
            permission: 'read' as 'read' | 'write',
            expiration: null as Date | null,
            // Gnration de lien
            linkPermission: 'read' as 'read' | 'write',
            linkExpiration: null as Date | null,
            linkLoading: false,
            shareLink: '',
            requireLogin: false,
            requireCode: false,
            accessCode: '',
            accessCodeError: '',
            generatedAccessCode: '',
            generateQr: false,
            qrCodeUrl: '',
            // Partages existants
            existingShares: [] as Share[],
            sharesLoading: false,
            // Options

            minDate: new Date(),
            document: null as Document | null,
            // Partage par service
            services: [] as Array<{id: string, nom: string}>,
            selectedService: null as string | null,
            serviceMessage: '',
            serviceNotification: false,
            serviceLoading: false,
            serviceError: '',
            serviceExpiration: null as Date | null,
            // Options de permissions
            permissionOptions: [
                { label: 'Lecture seule', value: 'read' },
                { label: 'Lecture et criture', value: 'write' }
            ],
        };
    },

    computed: {
        documentData(): Document | null {
            // On lit les donnes depuis la proprit 'document' passe par le parent
            return this.document;
        }
    },
    mounted() {
        if (this.dialogRef && this.dialogRef.data && this.dialogRef.data.document) {
            this.document = this.dialogRef.data.document;
            console.log('Document reu dans ShareDocument:', this.document);
            this.isVisible = true;
            
            // Normaliser le format du document si ncessaire
            // Les documents peuvent venir de diffrentes sources (vue dtaille, vue rcente)
            // et peuvent avoir des structures lgrement diffrentes
            if (!this.document.uuid && this.document.id) {
                this.document.uuid = this.document.id;
            }
        }
        this.loadServices();
    },
    methods: {
        closeDialog(refresh = false) {
            if (this.dialogRef) {
                this.dialogRef.close({ refresh });
            }
        },
        async loadServices() {
            try {
                const response = await apiClient.get('/services');
                const payload = response.data;
                let list: any[] = [];

                if (payload.data && Array.isArray(payload.data)) {
                    list = payload.data;
                } else if (Array.isArray(payload)) {
                    list = payload;
                }

                this.services = list.map(s => ({ nom: s.nom, id: s.id }));

            } catch (error) {
                console.error("Erreur lors du chargement des services:", error);
                if (this.toast) {
                    this.toast.add({ severity: 'error', summary: 'Erreur', detail: 'Impossible de charger les services.', life: 3000 });
                }
            }
        },
        validateEmail(emailStr: string): boolean {
            const re = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
            const isValid = re.test(emailStr);
            if (!isValid) {
                this.emailError = 'Veuillez entrer une adresse e-mail valide';
            } else {
                this.emailError = '';
            }
            return isValid;
        },
        formatDateForApi(date: Date): string {
            return date.toISOString().split('T')[0];
        },
        async shareDocument() {
            // Validation de l'email
            if (!this.validateEmail(this.email)) {
                this.toast?.add({ severity: 'error', summary: 'Erreur de validation', detail: 'Veuillez entrer une adresse e-mail valide.', life: 3000 });
                return;
            }
            
            // Vrification des donnes du document
            if (!this.documentData) {
                this.toast?.add({ severity: 'error', summary: 'Erreur critique', detail: 'Les donnes du document sont manquantes.', life: 3000 });
                return;
            }
            
            // Prparation des donnes  envoyer
            const payload = {
                email: this.email,
                message: this.message,
                permission_level: this.permission,
                notification: this.notification,
                expiration: this.expiration ? this.formatDateForApi(this.expiration) : null
            };
            
            // Log des donnes envoyes pour le debug
            console.log('Tentative de partage du document:', {
                documentUuid: this.documentData.uuid,
                payload
            });
            
            this.loading = true;
            try {
                const response = await apiClient.post(`/documents/${this.documentData.uuid}/share`, payload);
                console.log('Rponse du serveur aprs partage:', response.data);
                
                this.toast?.add({ severity: 'success', summary: 'Succs', detail: 'Document partag avec succs', life: 3000 });

                // **L'envoi d'e-mail est temporairement dsactiv**
                /*
                try {
                    await serviceApi.post(`/documents/${this.documentData.uuid}/send-email`, {
                        email: this.email,
                        message: this.message
                    });
                    this.toast?.add({ severity: 'info', summary: 'Information', detail: 'Un e-mail de partage a t envoy.', life: 3000 });
                } catch (emailError: any) {
                    console.error("Erreur dtaille lors de l'envoi de l'e-mail:", emailError.response || emailError);

                    let errorDetail = "l'envoi de l'e-mail a chou.";
                    if (emailError.response && emailError.response.data && emailError.response.data.message) {
                        errorDetail = `l'envoi de l'e-mail a chou: ${emailError.response.data.message}`;
                    }

                    this.toast?.add({ 
                        severity: 'warn', 
                        summary: 'Avertissement',
                        detail: `Le document a t partag, mais ${errorDetail}`,
                        life: 7000 
                    });
                }
                */

                this.closeDialog(true); // true pour indiquer qu'il faut rafrachir
            } catch (error: any) {
                console.error('Erreur lors du partage du document:', error);
                
                // Log dtaill de l'erreur pour le debug
                if (error.response) {
                    console.error('--- ERREUR SERVEUR ---');
                    console.error('STATUT:', error.response.status);
                    console.error('RPONSE:', JSON.stringify(error.response.data, null, 2));
                    console.error('--------------------');
                }
                
                let detail = 'Le partage du document a chou';
                
                // Gestion des erreurs de validation (422)
                if (error.response && error.response.status === 422 && error.response.data.errors) {
                    const errors = error.response.data.errors;
                    console.log('Erreurs de validation:', errors);
                    detail = Object.values(errors).flat().join(' ');
                }
                
                // Gestion des erreurs d'autorisation (403)
                else if (error.response && error.response.status === 403) {
                    detail = 'Vous n\'avez pas l\'autorisation de partager ce document.';
                }
                
                // Gestion des erreurs de ressource non trouve (404)
                else if (error.response && error.response.status === 404) {
                    detail = 'Le document que vous essayez de partager n\'existe pas ou a t supprim.';
                }
                
                this.toast?.add({ severity: 'error', summary: 'Erreur de validation', detail, life: 5000 });
            } finally {
                this.loading = false;
            }
        },
        async shareWithService() {
            // Validation du service
            if (!this.selectedService) {
                this.serviceError = 'Veuillez slectionner un service';
                this.toast?.add({ severity: 'error', summary: 'Erreur de validation', detail: 'Veuillez slectionner un service.', life: 3000 });
                return;
            } else {
                this.serviceError = '';
            }
            
            // Vrification des donnes du document
            if (!this.documentData) {
                this.toast?.add({ severity: 'error', summary: 'Erreur critique', detail: 'Les donnes du document sont manquantes.', life: 3000 });
                return;
            }
            
            // Prparation des donnes  envoyer
            const payload = {
                service_id: this.selectedService,
                message: this.serviceMessage,
                notification: this.serviceNotification,
                expiration: this.serviceExpiration ? this.formatDateForApi(this.serviceExpiration) : null
            };
            
            this.serviceLoading = true;
            try {
                const response = await apiClient.post(`/documents/${this.documentData.uuid}/share-by-service`, payload);
                
                this.toast?.add({ severity: 'success', summary: 'Succs', detail: 'Document partag avec le service avec succs', life: 3000 });
                this.closeDialog(true);
            } catch (error: any) {
                console.error('Erreur lors du partage avec le service:', error);
                
                let errorDetail = 'Une erreur est survenue lors du partage.';
                if (error.response?.data?.message) {
                    errorDetail = error.response.data.message;
                } else if (error.response?.data?.error) {
                    errorDetail = error.response.data.error;
                }
                
                this.toast?.add({ severity: 'error', summary: 'Erreur', detail: errorDetail, life: 5000 });
            } finally {
                this.serviceLoading = false;
            }
        },
        validateAccessCode() {
            // Valider que le code contient uniquement des chiffres
            if (this.accessCode && !/^\d+$/.test(this.accessCode)) {
                this.accessCodeError = 'Le code doit contenir uniquement des chiffres';
                return false;
            }
            // Valider que le code fait exactement 6 chiffres s'il est renseign
            if (this.accessCode && this.accessCode.length !== 6) {
                this.accessCodeError = 'Le code doit contenir exactement 6 chiffres';
                return false;
            }
            this.accessCodeError = '';
            return true;
        },
        async generateShareLink() {
            if (!this.documentData) {
                this.toast?.add({ severity: 'error', summary: 'Erreur', detail: 'Document non trouv', life: 3000 });
                return;
            }
            
            // Valider le code si protection par code active et code renseign
            if (this.requireCode && this.accessCode && !this.validateAccessCode()) {
                return;
            }
            
            this.linkLoading = true;
            try {
                const payload: any = {
                    permission_level: this.linkPermission,
                    expiration: this.linkExpiration ? this.formatDateForApi(this.linkExpiration) : null,
                    require_login: this.requireLogin,
                    require_code: this.requireCode,
                    generate_qr: this.generateQr
                };
                
                // Ajouter le code d'accs seulement s'il est renseign
                if (this.requireCode && this.accessCode) {
                    payload.access_code = this.accessCode;
                }
                
                console.log(' Gnration du lien avec les paramtres:', payload);
                
                const response = await apiClient.post(`/documents/${this.documentData.uuid}/share-link`, payload);
                
                // Transformer l'URL de l'API en URL frontend
                const apiUrl = response.data.data.share_url;
                // Extraire le token de l'URL API (ex: http://127.0.0.1:8000/api/shared-documents/TOKEN)
                const token = apiUrl.split('/').pop();
                // Crer l'URL frontend
                this.shareLink = `${window.location.origin}/shared/${token}`;
                
                // Rcuprer le code d'accs gnr si protection par code active
                if (this.requireCode && response.data.data.access_code) {
                    this.generatedAccessCode = response.data.data.access_code;
                }
                
                // Rcuprer le QR Code si gnr
                if (this.generateQr && response.data.data.qr_code_url) {
                    this.qrCodeUrl = response.data.data.qr_code_url;
                }
                
                this.toast?.add({ 
                    severity: 'success', 
                    summary: 'Succs', 
                    detail: 'Lien de partage gnr avec succs', 
                    life: 3000 
                });
            } catch (error: any) {
                console.error('Erreur lors de la gnration du lien:', error);
                
                let errorDetail = 'Impossible de gnrer le lien de partage';
                if (error.response?.data?.message) {
                    errorDetail = error.response.data.message;
                } else if (error.response?.data?.error) {
                    errorDetail = error.response.data.error;
                }
                
                this.toast?.add({ 
                    severity: 'error', 
                    summary: 'Erreur', 
                    detail: errorDetail, 
                    life: 5000 
                });
            } finally {
                this.linkLoading = false;
            }
        },
        openLink() {
            if (!this.shareLink) return;
            window.open(this.shareLink, '_blank');
        },
        async copyToClipboard(type: 'link' | 'code' = 'link') {
            const textToCopy = type === 'code' ? this.generatedAccessCode : this.shareLink;
            const label = type === 'code' ? 'Code' : 'Lien';
            
            if (!textToCopy) return;
            
            try {
                await navigator.clipboard.writeText(textToCopy);
                this.toast?.add({ 
                    severity: 'success', 
                    summary: 'Copi', 
                    detail: `${label} copi dans le presse-papiers`, 
                    life: 2000 
                });
            } catch (error) {
                // Fallback pour les navigateurs qui ne supportent pas l'API clipboard
                const textArea = document.createElement('textarea');
                textArea.value = textToCopy;
                document.body.appendChild(textArea);
                textArea.select();
                document.execCommand('copy');
                document.body.removeChild(textArea);
                
                this.toast?.add({ 
                    severity: 'success', 
                    summary: 'Copi', 
                    detail: `${label} copi dans le presse-papiers`, 
                    life: 2000 
                });
            }
        },
        async fetchExistingShares() {
            if (!this.documentData) return;
            this.sharesLoading = true;
            try {
                const response = await apiClient.get(`/documents/${this.documentData.uuid}/shares`);
                console.log('Rponse des partages existants:', response.data);
                const shares = Array.isArray(response.data) ? response.data : response.data.data;
                this.existingShares = shares.map((share: any) => ({
                    ...share,
                    permission_label: share.permission === 'read' ? 'Lecture seule' : 'Lecture et modification'
                }));
            } catch (error) {
                console.error('Erreur lors de la rcupration des partages:', error);
                this.toast?.add({ severity: 'error', summary: 'Erreur', detail: 'Impossible de rcuprer les partages existants', life: 3000 });
            } finally {
                this.sharesLoading = false;
            }
        },
        async removeShare(shareId: number) {
            if (!this.documentData) return;
            try {
                await apiClient.delete(`/documents/${this.documentData.uuid}/shares/${shareId}`);
                this.toast?.add({ severity: 'success', summary: 'Succs', detail: 'Partage supprim avec succs', life: 3000 });
                this.fetchExistingShares();
            } catch (error) {
                console.error('Erreur lors de la suppression du partage:', error);
                this.toast?.add({ severity: 'error', summary: 'Erreur', detail: 'La suppression du partage a chou', life: 3000 });
            }
        },
        formatDate(dateString: string): string {
            const date = new Date(dateString);
            return date.toLocaleDateString('fr-FR');
        },
        getIconForDocument(doc: Document) {
            const baseClass = 'pi';
            let icon = 'pi-file';
            let colorClass = 'text-gray-500';
            const type = doc.type_lisible || '';
            const name = doc.nom || '';
            if (type.startsWith('image')) {
                icon = 'pi-image';
                colorClass = 'text-red-500';
            } else if (type.includes('pdf')) {
                icon = 'pi-file-pdf';
                colorClass = 'text-red-700';
            } else if (type.includes('word') || /\.docx?$/.test(name)) {
                icon = 'pi-file-word';
                colorClass = 'text-blue-500';
            } else if (type.includes('excel') || /\.xlsx?$/.test(name)) {
                icon = 'pi-file-excel';
                colorClass = 'text-green-500';
            } else if (type.includes('zip') || /\.zip$/.test(name)) {
                icon = 'pi-file-zip';
                colorClass = 'text-yellow-600';
            }
            return `${baseClass} ${icon} ${colorClass}`;
        },
        formatSize(bytes: number) {
            if (bytes === 0) return '0 Bytes';
            const k = 1024;
            const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB'];
            const i = Math.floor(Math.log(bytes) / Math.log(k));
            return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
        },
        downloadQrCode() {
            if (!this.qrCodeUrl) return;
            
            // Crer un lien temporaire pour tlcharger l'image
            const link = document.createElement('a');
            link.href = this.qrCodeUrl;
            link.download = `qr-code-${this.documentData?.nom || 'document'}.png`;
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
            
            this.toast?.add({ 
                severity: 'success', 
                summary: 'Tlchargement', 
                detail: 'QR Code tlcharg avec succs', 
                life: 2000 
            });
        }
    },
    created() {
        // Injections are not automatically typed in Options API, so we handle them here.
        // dialogRef is passed via injection by PrimeVue's DialogService
        this.dialogRef = inject('dialogRef') as DialogRef;
        this.toast = useToast();
    },
});
</script>

